

.Memory-Card {
    display: grid;
    width: 164px;
    height: 164px;
    border-radius: 16px;
    overflow: hidden;
    background-color: var(--light-blue);
    grid-template-columns: 100%;
    grid-template-rows: 140px 24px;
    justify-items: center;
    align-items: center;
    /* z-index: -1; */
    margin: 20px;
}

.Memory-Card:hover {
    filter: brightness(110%);
    box-shadow: 4px 4px 8px var(--dark-blue);
    width: 170px;
    height: 170px;

}

.Memory-Card img {
    width: 128px;
    height: auto;
    
}